
export function transform(data) {
    return {
        front: frontData(data),
        client: dataClient(data),
        documentaryInformation: documentaryInformationData(data.documentaryInformation),
    }
}

function frontData(data) {
    return {
        title: `${validString(data.tradename).toUpperCase()}`,
        date: formatDate(validString(data.date)),
        datePretty: formatDatePretty(data.date),
        version: data.version? data.version : ""
    }
}

function validString(text) {
    if (!text) return '-';
    return `${text}`
}

function formatDate(date) {
    if (date == '-') return date;
    const temp = date.split("-");
    return temp.reverse().join('/')
}

function formatDatePretty(date) {
    if (date == '-') return date;
    const temp = date.split("-");
    return `${getMonthName(temp[1])} ${temp[0]}`
}

function getMonthName(number) {
    switch (number) {
        case `01`:
            return `ENERO`
        case `02`:
            return `FEBRERO`
        case `03`:
            return `MARZO`
        case `04`:
            return `ABRIL`
        case `05`:
            return `MAYO`
        case `06`:
            return `JUNIO`
        case `07`:
            return `JULIO`
        case `08`:
            return `AGOSTO`
        case `09`:
            return `SEPTIEMBRE`
        case `10`:
            return `OCTUBRE`
        case `11`:
            return `NOVIEMBRE`
        case `12`:
            return `DICIEMBRE`
        default:
            return `-`
    }
}
function dataClient(data) {
    return {
        tradename: validString(data.tradename),
        municipality: validString(data.municipality),
        date: formatDate(data.date),
        typeOfSetup: validString(data.typeOfSetup),
        whoAttendedUs: validString(data.whoAttendedUs),
    }
}
//Temporal.. se tiene que cambiar despues...
function documentaryInformationData(items) {
    if (!items) return [];
    const filter = items.filter(i => i.value == 'no');
    const cleanData = filter.map(i => {
        return {
            one: i.description,
            two: (i.nom) ? i.nom : `-`,
        }

    })
    return cleanData;
}