
import autoTable from "jspdf-autotable";
import { textTable } from "../table";
export function electricalContacts(doc, data) {
    superContact(doc, data);
}

function superContact(doc, data) {
    const get = data?.contactList.data;
    if (!get) return 0;
    if (get.length == 0) return 0;

    doc.addPage();
    textTable(doc, 'NO CONFORMIDADES - CONTACTOS');
    get.forEach(el => {
        tableBoard(doc, el);
        searchContact(doc, el);
    });


}
function searchContact(doc, el) {
    const dt = el?.result.itemsT;
    const cleanDt = dt.map(i => {
        if (i.value == "no") {
            return {
                number: i.number,
                model: i.model.toUpperCase(),
                specificPoint: i.specificPoint.toUpperCase(),
                specs: i.specs.toUpperCase()
            }
        }
    })
    if (cleanDt.length == 0) {
        console.log("No tiene");
    }
    if (cleanDt.length > 0) {
        tableContact(doc, cleanDt)
    }
}

function tableBoard(doc, data) {
    const tempData = [
        {
            one: "REFERENCIA",
            two: data.reference,
        },
        /*    {
               one: "NOMENCLATURA",
               two: data.nomenclature.toString().toUpperCase(),
           }, */
        {
            one: "LOCALIZACIÓN",
            two: data.location.toString().toUpperCase(),
        },
    ];

    autoTable(doc, {
        theme: 'plain',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 30 },
        },

        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: 8, textColor: "#17202A", cellPadding: 0.5 },
        margin: { left: 30, right: 35, },
    })
}


function tableContact(doc, data) {
    const sizeFontTable = 9;
    const tempDataTitle = [
        {
            number: "NO.",
            specificPoint: "PUNTO ESPECIFÍCO",
            model: "POLARIZACIÓN",
            specs: "OBSERVACIONES"
        },
    ];
    const tempData = [
        ...tempDataTitle,
        ...data
    ]
    autoTable(doc, {
        theme: 'grid',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 50 },

        },
        columnStyles: {
            number: { cellWidth: 10, halign: 'center' },
            specificPoint: { cellWidth: 45, halign: 'center' },
            model: { cellWidth: 45, halign: 'center' },
            specs: { halign: 'center' },
        },
        columns: [
            { dataKey: "number" },
            { dataKey: "specificPoint" },
            { dataKey: "model" },
            { dataKey: "specs" },

        ],
        styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5, lineColor: "#17202A" },
        margin: { left: 30, right: 30, },
        didParseCell: data => {
            if (data.row.index == 0) {
                data.cell.styles.textColor = `#FFFFFF`;
                data.cell.styles.fillColor = `#85C1E9`;
            }

        }
    })
    doc.setDrawColor('#17202A');
    doc.setLineWidth(0.6);
    doc.setLineDash([1, 1], 0);
    doc.line(30, doc.lastAutoTable.finalY + 3, 180, doc.lastAutoTable.finalY + 3);
    doc.setLineDash([0, 0], 0);
}
