import jsPDF from "jspdf";
import { transform } from "../adapt_data";
import { electricalLand } from "./table";
import { frontPageLands, imgFooter } from "../front";
let front;

//Funcion principal...
export function generateLand(data) {
    create(data);
};

export function create(data) {
    const get = data.landList.data;
    if (!get) return 0;
    if (get.length == 0) return 0;
    front = transform(data).front;
    //Start pdf
    const doc = new jsPDF();
    frontPageLands(doc, front);
    electricalLand(doc, get);
    //Footers
    addFooters(doc);
    //window.open(doc.output('bloburl'));
    doc.save(`${data.tradename.toUpperCase()} - LECTURA DEL SISTEMA DE PUESTA A TIERRA`);
}


function addFooters(doc) {
    const pageCount = doc.internal.getNumberOfPages()
    doc.setFont('helvetica', 'italic')
    doc.setFontSize(8)
    for (var i = 1; i <= pageCount; i++) {
        imgFooter(doc, 17, 282);
        doc.text('GRUPO VIVE / Controla el riesgo.', 25, 290)
        doc.setPage(i)
        doc.text('Informe técnico realizado con UVIEREPORT®', doc.internal.pageSize.width / 2, 290, {
            align: 'center'
        })
        if (i > 1) {
            doc.text('Página ' + String(i) + ' de ' + String(pageCount), 160, 290)
        }
    }
}