
import autoTable from "jspdf-autotable";
import { textTable } from "../table";

export function electricalAreas(doc, data) {
    superAreas(doc, data)
}

function superAreas(doc, data) {

    const get = data?.areaList.data;
    if (!get) return 0;
    if (get.length == 0) return 0;

    doc.addPage();
    textTable(doc, 'NO CONFORMIDADES - ÁREAS');
    const formData = format(get);

    console.log(formData);

    table(doc, formData)
    //console.log(get);
}

function format(data) {
    let dataForm = [];
    let departure = 0;

    data.forEach(e => {
        e.result.itemsT.forEach(p => {
            console.log(p);

            p.items.forEach(i => {
                if (i.value) {
                    departure++;
                    const dt = {
                        departure: departure,
                        area: e.location,
                        observation: i.label,
                        specificPoint: p.specificPoint,
                        section: i.nom,
                    }
                    dataForm.push(dt);
                }
            })
        })
    });
    return dataForm;

    /* 
    departure
    Area
    Observation
    specific point
    Section */
}



function table(doc, data) {
    const sizeFontTable = 8;
    const tempDataTitle = [
        {
            departure: "NO.",
            area: "ÁREA",
            observation: "OBSERVACIÓN",
            specificPoint: "PUNTO ESPECÍFICO",
            section: "SECCIÓN",
        },
    ];
    data = data.map(i => {
        const temp = i.section == "408" ? "400-8" : i.section;
        return {
            departure: i.departure,
            area: i.area.toUpperCase(),
            observation: i.observation.toUpperCase(),
            specificPoint: i.specificPoint.toUpperCase(),
            section: temp.toUpperCase(),
        }

    })
    const tempData = [
        ...tempDataTitle,
        ...data
    ]


    autoTable(doc, {
        theme: 'grid',
        body: tempData,


        columnStyles: {
            departure: { cellWidth: 15, halign: 'center' },
            area: { cellWidth: 25, halign: 'center' },
            observation: { cellWidth: 59, halign: 'center' },
            specificPoint: { cellWidth: 35, halign: 'center' },
            section: { cellWidth: 16, halign: 'center' },
            /*    specificPoint: { cellWidth: 45 },
               photoNumber: { cellWidth: 25 }, */
        },
        columns: [
            { dataKey: "departure" },
            { dataKey: "area" },
            { dataKey: "observation" },
            { dataKey: "specificPoint" },
            { dataKey: "section" },

        ],
        styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5, lineColor: "#17202A" },
        margin: { left: 30, right: 30, },
        didParseCell: data => {
            if (data.row.index == 0) {
                data.cell.styles.textColor = `#FFFFFF`;
                data.cell.styles.fillColor = `#85C1E9`;
                data.cell.styles.halign = `center`;
            }

        }
    })
    doc.setDrawColor('#17202A');
    doc.setLineWidth(0.6);
    doc.setLineDash([1, 1], 0);
    doc.line(30, doc.lastAutoTable.finalY + 3, 180, doc.lastAutoTable.finalY + 3);
    doc.setLineDash([0, 0], 0);
} 