import jsPDF from "jspdf";
import { frontPageHotSpots } from "../front";
import { transform } from "../adapt_data";
import { generateTable } from "../table";
import { superHosPots, superElectricalTransformers, superRush } from "./table_hotspots"
let dataCopy;
let superData;
let front;


export function generateHotSpots(data) {
    dataCopy = data
    superData = transform(data);
    front = superData.front;
    create();
}

export function create() {
    const doc = new jsPDF();
    frontPageHotSpots(doc, front);
    generateTable(doc, dataCopy, superData);
    superHosPots(doc, dataCopy);
    superElectricalTransformers(doc, dataCopy);
    superRush(doc, dataCopy);
    //window.open(doc.output('bloburl'))
    doc.save(`${dataCopy.tradename.toUpperCase()} - PUNTOS CALIENTES`);
}


