//transformers
//substations

export function electricalSubstations(doc, data) {
    let allNoms = [];
    if (!data) return 0;
    if (!data.substationsList) return 0;

    const get = data?.substationsList.data;
    if (!get) return 0;
    if (get.length == 0) return 0;
    doc.addPage();
    textTable(doc, 'NO CONFORMIDADES - SUBESTACIÓNES');
    get.forEach(el => {
        tableSubstations(doc, el);
        let cleanData = el.result.filter(i => {
            if (i.value == "no") {
                return i
            }
        })

        cleanData = cleanData.map(i => {
            if (i.value == "no") {
                return {
                    //one: findSubstation(i),
                    one: findSubstation(i.nom, 'one'),
                    two: i.nom,
                }
            }
        })
        allNoms = [...allNoms, ...cleanData];

        observation(doc, cleanData);
    });
    //Se crea el glosario para subestaciones...
    const clean = [...new Set(allNoms.map(i => i.two))];
    const glossary = clean.map(i => {
        return {
            one: findSubstation(i, 'three'),
            two: i
        }
    })
    return glossary;
}
//Transformadores..

export function electricalTransformers(doc, data) {
    if (!data) return 0;
    if (!data.transformersList) return 0;
    const get = data?.transformersList.data;
    if (!get) return 0;
    if (get.length == 0) return 0;
    doc.addPage();
    textTable(doc, 'NO CONFORMIDADES - TRANSFORMADORES');
    get.forEach(el => {
        tableTransformers(doc, el);
        searchTransformers(doc, el.result, el);

    });
};

function tableSubstations(doc, data) {
    const tempData = [
        {
            one: "REFERENCIA",
            two: validTextSup(data.reference),
        },
        {
            one: "LOCALIZACIÓN",
            two: validTextSup(data.location),
        },
        {
            one: "NOMBRE",
            two: validTextSup(data.name),
        },
    ];

    autoTable(doc, {
        theme: 'plain',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 30 },
        },

        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: 9, textColor: "#17202A", cellPadding: 0.5 },
        margin: { left: 30, right: 35, },
    })
}




function observation(doc, data, note, items) {

    //Guardar datos de los items...

    const sizeFontTable = 9;
    let tempDataTitle = [
        {
            one:
                "OBSERVACIÓN ",
            two: 'SECCIÓN',

        },
    ];

    if (data.length > 0) {
        tempDataTitle.push({
            one:
                "Debe presentar constancia de mantenimiento del transformador, donde se incluyan: relación de transformación, resistencia de aislamiento, rigidez dieléctrica del aceite.",
            two: '-',

        },)
    } else {
        tempDataTitle.push({
            one:
                "No se encontraron no conformidades.",
            two: '-',

        })



    }

    const tempData = [
        ...tempDataTitle,
        ...data
    ]
    /* suponiento que hay datos en data.. hay que fusionar los datos de información documental */

    autoTable(doc, {
        theme: 'plain',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 130 },
            two: { halign: 'center' },
        },
        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5 },
        margin: { left: 30, right: 30, },
        didParseCell: data => {

            if (data.row.index == 0) {
                data.cell.styles.textColor = `#FFFFFF`;
                data.cell.styles.fillColor = `#85C1E9`;
            }

            if (data.row.cells[1].raw == '110-14.C') {
                data.cell.styles.textColor = `#FFFFFF`;
                data.cell.styles.fillColor = `#922B21`;
            }

            if (data.row.cells[1].raw == '408-40') {
                data.cell.styles.textColor = `#FFFFFF`;
                data.cell.styles.fillColor = `#922B21`;
            }
        }
    })

    //Agregar fugas de aceite antes que las notas..
    //Traer items...
    oilLeaks(doc, items, true);

    if (note && note.value) {

        if (note.value.replace(/ /g, "") != "") {
            autoTable(doc, {
                theme: 'plain',
                body: [['Notas'], [note.value]],
                styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5 },
                margin: { left: 30, right: 30, },
            })
        }

    }


    doc.setDrawColor('#17202A');
    doc.setLineWidth(0.6);
    doc.setLineDash([1, 1], 0);
    doc.line(30, doc.lastAutoTable.finalY + 3, 180, doc.lastAutoTable.finalY + 3);
    //doc.setLineWidth(1);
    doc.setLineDash([0, 0], 0);
}


