function searchTransformers(doc, data, transformers) {
    if (doc) console.log("bien")
    console.log(data);
    let note = transformers.result.find(f => f.content == "ItemNote");
    const cleanData = data.filter(e => {
        if (e.content == "ItemBasicDate" || e.content == "ItemBasic") {
            if (e.value == "no") {
                return e
            }

        }
        if (e.content == "ItemThermography" && e.nom == "110-14.C") {
            if (e.value == "no") {
                return e
            }
        }

    })

    let clean = cleanData.map(i => {
        return {
            //one: i.description,
            one: findTransformers(i),
            //Validacion temporal..
            two: i.nom == "923-3.C" ? '923-3.D' : i.nom,
        }
    })
    if (transformers && transformers.oilLevel) {
        const level = transformers.oilLevel;
        if (level == 'MEDIO' || level == 'BAJO') {
            clean.unshift(
                {
                    one: 'DEBE NORMALIZAR NIVEL DE ACEITE.',
                    two: '924-14',
                }
            )
        }
    }
    observation(doc, clean, note, transformers.result);
}
//Validacion para pintar el texto..
function validTextSup(data) {
    if (!data) return "-";
    return data.toString().toUpperCase();
}

function findSubstation(i, properties) {
    return (glossarySubstations.find(e => e.two == i)[properties]);
}

function findTransformers(i) {
    const result = glossaryTransformers.find(e => e.two == i.nom);
    //console.log(`Resultado de busqueda ${result}`);
    return result ? result.one : i.description;
}

