//Acometidas...pilon..
export function electricalRush(doc, data) {
    if (!data) return 0;
    if (!data.rushList) return 0;
    const get = data?.rushList.data;
    if (!get) return 0;
    if (get.length == 0) return 0;
    doc.addPage();
    textTable(doc, 'NO CONFORMIDADES - MURETE DERIVADOR');
    get.forEach(el => {
        tableRush(doc, el);
        let note = el.result.find(f => f.content == "ItemNote");
        let cleanData = el.result.filter(i => {
            if (i.value == "no") {
                return i
            }
        })

        cleanData = cleanData.map(i => {
            if (i.value == "no") {
                return {
                    //one: findSubstation(i),
                    one: findRush(i),
                    two: i.nom,
                }
            }
        })
        console.log("Datos de acometidas..")
        console.log(cleanData);
        observation(doc, cleanData, note);
    });
};

