import autoTable from "jspdf-autotable";
import { textTable, tableBoard } from "../table";
import { tableTransformers, tableRush} from "../substations-transformers";

export function superHosPots(doc, data) {
    superTableBoardHosPots(doc, data)
}


export function superTableBoardHosPots(doc, data) {
    if (!data) return 0;
    if (!data.boardList) return 0;
    if (!data.boardList.data) return 0;
    if (data.boardList.data.length == 0) return 0;
    textTable(doc, 'PUNTOS CALIENTES - TABLEROS ELÉCTRICOS');
    const list = data.boardList.data;
    listBoard(doc, list);
}

function listBoard(doc, list) {
    const arr = list;
    if (Array.isArray(arr)) {
        arr.forEach(element => {
            tableBoard(doc, element)
            searchItem(doc, element.result)
        });
    }
}

function searchItem(doc, items) {
    let find = items.find(e => e.nom == '110-14.C')
    if(!find){
        textSubTable(doc, 'Sin datos');
        return 0;
    }
    if (!find.value) {
        console.log('sin datos');
        console.log(find);
        tableNOandNullHostPots(doc)
    }
    if (find.value == "yes") {
        console.log(find.overallPhoto);
        tableNOandNullHostPots(doc, find.overallPhoto)
    }
    if (find.value == "no") {
        console.log('Puntos calientes.');
        console.log(find.itemsT);
        tableYesHostPots(doc, find.itemsT)
    }
}

function tableYesHostPots(doc, data) {
    const sizeFontTable = 9;
    const tempDataTitle = [
        {
            number: "No.".toUpperCase(),
            photoNumber: "No. De Foto".toUpperCase(),
            specificPoint: "Punto Específico".toUpperCase(),
            specs: "Especificaciones".toUpperCase()
        },
    ];
    const tempData = [
        ...tempDataTitle,
        ...data
    ]
    autoTable(doc, {
        theme: 'grid',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 50 },

        },
        columnStyles: {
            number: { cellWidth: 10, halign: 'center' },
            specificPoint: { cellWidth: 45 },
            photoNumber: { cellWidth: 25 },
        },
        columns: [
            { dataKey: "number" },
            { dataKey: "specificPoint" },
            { dataKey: "photoNumber" },
            { dataKey: "specs" },

        ],
        styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5, lineColor: "#17202A" },
        margin: { left: 30, right: 30, },
        didParseCell: data => {
            if (data.row.index == 0) {
                data.cell.styles.textColor = `#FFFFFF`;
                data.cell.styles.fillColor = `#922B21`;
            }

        }
    })
}

function tableNOandNullHostPots(doc, data) {
    const sizeFontTable = 9;

    let tempData;
    if (data) {
        tempData = [
            "No. de Foto General",
            data
        ];

    } else {
        tempData = ['Sin datos']
    }

    autoTable(doc, {
        theme: 'grid',
        body: [tempData],
        styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5, lineColor: "#17202A" },
        margin: { left: 30, right: 30, },
    })
}

///Transformadores...


export function superElectricalTransformers(doc, data) {
    if (!data) return 0;
    if (!data.transformersList) return 0;
    const get = data?.transformersList.data;
    if (!get) return 0;
    if (get.length == 0) return 0;
    doc.addPage();
    textTable(doc, 'NO CONFORMIDADES - TRANSFORMADORES');
    get.forEach(el => {
        tableTransformers(doc, el);
        textSubTable(doc, '110-14.C - Conexiones eléctricas. c) Limitaciones por temperatura.');
        searchItem(doc, el.result);
        textSubTable(doc, 'FOTOS TERMOGRAFICAS');
        thermographicPhotos(doc, el.result);
        
        oilLeaks(doc, el.result);
    });
};


function textSubTable(doc, text) {
    autoTable(doc, {
        //startY: doc.lastAutoTable.finalY + 5,
        theme: 'plain',
        body: [[text]],
        styles: { fontSize: 12, textColor: "#17202A", fontStyle: 'bold', cellWidth: 500 },
        margin: { left: 30, right: 35, },
    })
}


function thermographicPhotos(doc, items){
    if(!items)return textSubTable(doc, 'Sin datos');
    let find = items.find(e => e.nom == 'FOTOS TERMOGRAFICAS');
    if(!find || !find.itemsT || find.itemsT.length == 0) return textSubTable(doc, 'Sin datos');
    tableThermographicPhotos(doc, find.itemsT)
}

export function oilLeaks(doc, items, hidden){
    if(!items)return hidden ? 0 : textSubTable(doc, 'Sin datos');
    let find = items.find(e => e.nom == 'FUGAS DE ACEITE');
    console.log(find)
    if(!find || !find.itemsT || find.itemsT.length == 0) return hidden ? 0 : textSubTable(doc, 'Sin datos');
    //textSubTable(doc, 'FUGAS DE ACEITE');
    tableOilLeaks(doc, find.itemsT);
}


