export function superRush(doc, data){
    if (!data) return 0;
    if (!data.rushList) return 0;
    const get = data?.rushList.data;
    if (!get) return 0;
    if (get.length == 0) return 0;
    doc.addPage();
    textTable(doc, 'NO CONFORMIDADES PUNTOS CALIENTES - MURETE DERIVADOR');
    get.forEach(el => {
        tableRush(doc, el);
        searchItem(doc, el.result);
    });
}

