import jsPDF from "jspdf";
import { frontPage, imgFooter } from "./front";
import { transform } from "./adapt_data";
import { generateTable, superTableBoardItems, noConformitiesGeneral, regulatorySupport, generateTableDocumentation } from "./table";
import { electricalContacts } from "./electrical-contacts/index";
import { electricalAreas } from "./electrical-areas/index";
import { electricalSubstations, electricalTransformers, electricalRush } from "./substations-transformers";
let dataCopy;
let superData;
let front;
/* Hay que transformar los datos para que se puedan entender correctamente.. */
export function generate(data) {
    dataCopy = data
    superData = transform(data);
    front = superData.front;
    create();
}

export function create() {

    /*    
    Cambiar el orden del reporte 
   1 documental
   --Acometida
   2 subestaciones
   3 transfos
   4 tableros
   5 áreas 
   6 contactos
   7 notas */
    const doc = new jsPDF();
    frontPage(doc, front);
    generateTable(doc, dataCopy, superData);
    generateTableDocumentation(doc, superData);
    electricalRush(doc,dataCopy);
    const glossarySub = electricalSubstations(doc, dataCopy);
    electricalTransformers(doc, dataCopy);
    superTableBoardItems(doc, dataCopy);
    electricalAreas(doc, dataCopy);
    electricalContacts(doc, dataCopy);
    noConformitiesGeneral(doc, dataCopy);
    //Agregar glosario de Subestaciones..
    regulatorySupport(doc, glossarySub);
    addFooters(doc);
    //window.open(doc.output('bloburl'))
    doc.save(`${dataCopy.tradename.toUpperCase()} - DICTAMEN DE LAS INSTALACIONES ELÉCTRICAS 2023`);
}


function addFooters(doc) {
    const pageCount = doc.internal.getNumberOfPages()
    doc.setFont('helvetica', 'italic')
    doc.setFontSize(8)
    for (var i = 1; i <= pageCount; i++) {
        imgFooter(doc, 17, 282);
        doc.text('GRUPO VIVE / Controla el riesgo.', 25, 290)
        doc.setPage(i)
         doc.text('Informe técnico realizado con UVIEREPORT®', doc.internal.pageSize.width / 2, 290, {
                align: 'center'
            }) 
        if (i > 1) {
            doc.text('Página ' + String(i) + ' de ' + String(pageCount), 160, 290)
        }
    }
}