
import autoTable from "jspdf-autotable";
import { textTable } from "../table";

export function electricalLand(doc, data) {
    data.forEach(el => {
        tableLands(doc, el);
        searchLands(doc, el.result[0].itemsT);
        //doc.line(30, doc.lastAutoTable.finalY + 3, 180, doc.lastAutoTable.finalY + 3);
        doc.setDrawColor('#17202A');
        doc.setLineWidth(0.6);
        doc.setLineDash([1, 1], 0);
        doc.line(30, doc.lastAutoTable.finalY + 3, 180, doc.lastAutoTable.finalY + 3);
        doc.setLineDash([0, 0], 0);

    });

    regulatorySupportTable(doc);
}

export function tableLands(doc, data) {
    //console.log(doc.lastAutoTable.finalY);


    const tempData = [
        {
            one: "REFERENCIA",
            two: validTextSup(data.reference),
        },
        {
            one: "PUNTO DE MEDICIÓN",
            two: validTextSup(data.measuringPoint),
        },

        {
            one: "LOCALIZACIÓN",
            two: validTextSup(data.location),
        },

        {
            one: "NOMENCLATURA",
            two: validTextSup(data.nomenclature),
        },
    ];

    autoTable(doc, {
        theme: 'plain',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 50 },
        },

        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: 9, textColor: "#17202A", cellPadding: 0.5 },
        margin: { left: 30, right: 35, },
        didParseCell: data => {
            if (data.column.dataKey == "one") {
                data.cell.styles.fontStyle = 'bold';
            }
        }
    })

}
function searchLands(doc, data) {
    data.forEach(item => {
        tableLandsItems(doc, item)
    })
}


export function tableLandsItems(doc, data) {
    //console.log(doc.lastAutoTable.finalY);
    if (doc.lastAutoTable.finalY > 230) {
        doc.addPage();
    }
    const red = data.observation;
    const tempData = [
        {
            one: "PUNTO ESPECÍFICO",
            two: validTextSup(data.specificPoint),
        },
        {
            one: "VALOR MEDIDO (ohm)",
            two: validTextSup(data.measuredValue),
        },

        {
            one: "VALOR MÁXIMO PERMITIDO (ohm)",
            two: validTextSup(data.type?.maxValue),
        },

        {
            one: "OBSERVACIÓN",
            two: validTextSup(data.observation),
        },
        {
            one: "",
            two: "",
        },
        {
            one: "NOTA",
            two: validTextSup(data.notes),
        },
    ];

    autoTable(doc, {
        theme: 'plain',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 60 },
        },

        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: 9, textColor: "#17202A", cellPadding: 0.5 },
        margin: { left: 80, right: 20, },
        didParseCell: data => {
            if (data.row.index == 0 && data.column.dataKey == "one") {
                data.cell.styles.fontStyle = 'bold';
            }
            //if(data.column.dataKey == "two") data.cell.styles.cellWidth = "auto";
            if (data.row.index == 3 && data.column.dataKey == "two") {
                if (red == "NO CUMPLE") {
                    data.cell.styles.textColor = `#FFFFFF`;
                    data.cell.styles.fillColor = `#922B21`;
                    //data.cell.styles.cellWidth = 20;
                }

            }



        }
    })

}


function validTextSup(data) {
    if (!data) return "-";
    return data.toString().toUpperCase();
}

function regulatorySupportTable(doc) {
    doc.addPage()
    textTable(doc, 'SOPORTE NORMATIVO.');

    autoTable(doc, {
        theme: 'plain',
        body: textRegulatory,
        columnStyles: {
            two: { cellWidth: 1 },
        },
        columns: [
            { dataKey: "two" },
            { dataKey: "one" },

        ],
        styles: { fontSize: 9, textColor: "#17202A", cellPadding: 2, halign: 'justify' },
        //margin: { left: 20, right: 30, },
    })
}

